import * as React from "react";

import {BonusReward, ExtraBonusReward } from "../../lib/experiment/config";
export const TrainingIntro: React.StatelessComponent<{onClick: () => void}> = (p) => {
  return <>
    <div className="introSection">
      <h2>Analyze flights fun! ✈️</h2>
      <p>
        You will be viewing an interactive visualization dashboard of <b>flights to Hawaii from other US states, in December 2017</b>.
        It visualizes the information about flights containing information about their departure state, the day of the month,
        flight carrier, and delay information.
      </p>
      <p>
        We have synthesized the data based on real data. Please <strong>do not</strong> use your existing
        knowledge to complete the tasks. Also please do not Google the results---it will not be needed and interferes
        with our results.
      </p>
    </div>
    <p><b>
    We will walk you through a tutorial and a set of tasks. If you answer all of the initial three tasks incorrectly,
    you will be disqualified from the HIT.
    </b></p>
    <p>
      The training questions will be over the data from the first half of December,
      and the actual tasks will be over the second half of December.
      The visualization tool will stay the same, and the questions similar.
    </p>
    <div className="introSection">
    <h2>Time!⏱️</h2>
      <p>
        You are encouraged to complete the tasks both accurately and efficiently. Please try not to take breaks <strong>during </strong>
        a task (usually less than 1 minute to 3 minutes), but feel free to take breaks <strong>between</strong> tasks.
        If we detect that you left the window <strong>during</strong> a task, you might not be paid the bonus in full.
        We may send out up to ${ExtraBonusReward.toFixed(2)} of additional reward for those who performed effectively (in addition to
        the base ${BonusReward.toFixed(2)}).
      </p>
    </div>
    <button className="general-btn exp-nav-btn" onClick={p.onClick}>next</button>
    </>;
};



