import * as React from "react";

import { diel } from "../../diel/setup";
import { TaskStep, TaskQA, TaskTypes, TaskConfidence } from "../../lib/experiment/tasks";

interface TaskQuestionProps {
  isTraining: boolean;
  question: TaskQA;
  nextTask: () => void;
}

interface TaskState {
  step: TaskStep;
  answer: string;
  confidence: TaskConfidence;
  answerValue: number;
  comment: string;
  timeElapsed: number;
  timer: number;
  warn: boolean;
}

function setUpHelper(q: TaskQA, isTraining: boolean) {
  diel.LogData.taskInfo({
    $qId: q.qId,
    $taskType: TaskTypes[q.type],
    $isTraining: isTraining ? 1 : 0
  });
  if (q.type === TaskTypes.Recall) {
    diel.LogData.memoryTask({$qId: q.qId, $linkedQId: q.linkedQId});
  }
}

export default class Task extends React.Component<TaskQuestionProps, TaskState> {

  constructor(props: TaskQuestionProps) {
    super(props);
    this.nextStep = this.nextStep.bind(this);
    this.onChangeAnswer = this.onChangeAnswer.bind(this);
    this.onCommentChange = this.onCommentChange.bind(this);
    this.setConfidence = this.setConfidence.bind(this);
    this.tick = this.tick.bind(this);
    // by default show the most recent unless otherwise specified
    this.state = {
      step: TaskStep.Description,
      answer: null,
      confidence: null,
      answerValue: null,
      comment: null,
      timer: null,
      timeElapsed: 0,
      warn: false
    };
    diel.SetFreeze(true);
    const q = this.props.question;
    setUpHelper(q, this.props.isTraining);
  }

  tick() {
    this.setState({
      timeElapsed: this.state.timeElapsed + 1
    });
  }

  componentWillReceiveProps(p: TaskQuestionProps) {
    // reset state, but make sure to check that the props has changed...
    if (p.question.qId !== this.props.question.qId) {
      clearTimeout(this.state.timer);
      setUpHelper(p.question, this.props.isTraining);
      this.setState({
        answer: null,
        confidence: null,
        timeElapsed: 0,
        step: TaskStep.Description
      });
    }
  }

  onChangeAnswer(event: any) {
    const answer = event.target.value;
    const answerValue = event.target.getAttribute("data-score");
    console.log("data score", answerValue);
    this.setState({ warn: false, answer, answerValue });
  }

  setConfidence(confidence: TaskConfidence) {
    this.setState({warn: false, confidence});
  }

  onCommentChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
    this.setState({ comment: event.target.value });
  }

  // look into if I need to not use logEvents?
  nextStep(step: TaskStep) {
    const qId = this.props.question.qId;
    console.log("is this event called", step);
    if (step === TaskStep.Answering) {
      if (this.props.question.type !== TaskTypes.Recall) {
        diel.SetFreeze(false);
      }
      diel.LogData.taskTimes({$qId: qId, $eventType: "start"});
      console.log("logged start");
      this.setState({
        timer: window.setInterval(this.tick, 1000),
        step
      });
      return;
    }
    if (step === TaskStep.Submitted) {
      if (this.state.answer === null || this.state.confidence === null) {
        // must have an asnwer
        this.setState({
          warn: true,
        });
        return;
      }
      clearInterval(this.state.timer);
      diel.SetFreeze(true);
      diel.LogData.taskTimes({$qId: qId, $eventType: "end"});
      console.log("logged end");
      this.setState({ step });
      return;
    }
  }

  render() {
    let { question, nextTask } = this.props;
    let { step, answer, confidence } = this.state;
    const questionDiv =  <p className="question" dangerouslySetInnerHTML={{ __html: question.question }}></p>;
    const choices = question.choices.map(q => <label><input
      required={true}
      type="radio"
      disabled={(step !== TaskStep.Answering)}
      value={q.text}
      checked={answer ? answer.indexOf(q.text) > -1 : false}
      data-score={q.value}
      onChange={this.onChangeAnswer}
    ></input>
      {q.text}
    </label>);
    const setConfidence = this.setConfidence;
    function _genRadio(c: TaskConfidence, text: string) {
      return <label><input
        required={true}
        type="radio"
        disabled={(step !== TaskStep.Answering)}
        value={TaskConfidence[c]}
        checked={confidence === c}
        onChange={() => setConfidence(c)}
      ></input>{text}
      </label>;
    }
    const confidenceDiv = <>
     <p className="question">How confident are you?</p>
    <div className="radio-button-wrapper">
      {_genRadio(TaskConfidence.NotAtAll, "Not At All")}
      {_genRadio(TaskConfidence.Somewhat, "Somewhat")}
      {_genRadio(TaskConfidence.Very, "Very")}
  </div></>;
    let additional: JSX.Element;
    let button: JSX.Element;
    if (step === TaskStep.Description) {
      button = <><button
      className="general-btn exp-nav-btn"
      onClick={() => this.nextStep(TaskStep.Answering)}>
        Start Task
      </button><span className="footnote">click to activate</span></>;
    } else if (step === TaskStep.Answering) {
      button = <button
        className="general-btn submit-btn"
        onClick={() => this.nextStep(TaskStep.Submitted)}>
        Submit
      </button>;
    } else if (step === TaskStep.Submitted) {
      button = <button
        className="general-btn exp-nav-btn"
        onClick={() => {
          // if there is an anwer, log that, as well as the comment
          diel.LogData.answers({
            $qId: this.props.question.qId,
            $answer: this.state.answer,
            $confidence: TaskConfidence[this.state.confidence],
            $score: this.state.answerValue,
            $comment: this.state.comment
          });
          nextTask();
        }}>
          Next Task
      </button>;
      let answerDiv: JSX.Element;
      // note that we do not include partial answers in the instructions
      const correctAnswers = question.choices.filter(c => c.value === 1);
      const correct = correctAnswers.filter(c => c.text === this.state.answer).length > 0;
      if (correct) {
        answerDiv = <div>
          <hr/>
          <p className="correct">Correct!</p>
          {correctAnswers.length > 1
            ? <p>Other possible correct answers: {correctAnswers.filter(c => c.text !== this.state.answer).map(c => c.text + " ")}</p>
            : null}
        </div>;
      } else {
        answerDiv = <div>
          <hr/>
          <p className="warning">Wrong.</p>
          {correctAnswers.length > 1
            ? <p>Possible correct answers is any of the following: {correctAnswers.map(c => c.text + " ")}</p>
            : <p>Correct answer: {correctAnswers[0].text}</p>}
        </div>;
      }
      additional = <>
        {answerDiv}
        <hr/>
        <p className="footnote">💡 Feel free to take a break now before you start your next question!
          Otherwise, please try to complete the next task in one sitting.</p>
      </>;
    }
    return <div>
      {this.props.question.type === TaskTypes.Recall ?
        this.props.isTraining ?
          <p>
            In this question we are testing your ability to recall some
            information you might have seen while doing the previous task ("memory question").
            You cannot interact with the visualizations for these questions
            ---try your best to remember.</p>
          : <p>Memory Question</p>
        : null}
      {questionDiv}
      {this.state.warn ? <p className="warning">Please answer both questions</p> : null}
      <div className="radio-button-wrapper">
        {choices}
      </div>
      {confidenceDiv}
      {additional}
      <div style={{height: 5}}></div>
      {step === TaskStep.Submitted
        ? <>
        <label htmlFor="comments"><i>Optional</i> note on how you approached the problem</label>
        <br/>
        <textarea
          rows={5}
          cols={39}
          id="comments"
          name="comments"
          onChange={this.onCommentChange}></textarea></>
        : null}
      {button}
      {step === TaskStep.Description
        ? null
        : <p style={{color: "gray"}}>Time {step === TaskStep.Submitted ? "Taken" : ""}: {this.state.timeElapsed} s</p>
      }    </div>;
  }
}


