import * as React from "react";

interface PopupProps {
  title: string;
  videoLink: string;
  texts: string[];
  next: () => void;
  prev: () => void;
}

export const VideoPopup: React.StatelessComponent<PopupProps> = (p) => {
  let { next, videoLink, texts, title, prev } = p;
  return (
    <div className="popup">
      <div className="popup-inner">
        <div style={{margin: "auto"}}>
          <h3 style={{textAlign: "center"}}>{title}</h3>
          <div className="description">
            {texts.map(t => <p>{t}</p>)}
          </div>
          <video
            width={400}
            autoPlay
            src={videoLink}
            controls loop
          ></video>
          <br/>
          <button className="general-btn" onClick={prev} disabled={prev === null}>prev</button>
          <button className="general-btn exp-nav-btn" onClick={next}>next</button>
        </div>
      </div>
    </div>
  );
};



