import { IsChronicles } from "../../lib/experiment/config";

export const FlowSurveyQuestions = new Map<string, string>();
// creating hashes as a sanity check
FlowSurveyQuestions.set("flowChallenge", "I feel just the right amount of challenge");
FlowSurveyQuestions.set("flowSmooth", "My thoughts/activities run fluidly and smoothly");
FlowSurveyQuestions.set("flowTime", "I do not notice time passing");
FlowSurveyQuestions.set("flowConcentrate", "I have no difficulty concentrating");
FlowSurveyQuestions.set("flowClear", "My mind is completely clear");
FlowSurveyQuestions.set("flowAbsorbed", "I am totally absorbed in what I am doing");
FlowSurveyQuestions.set("flowAccord", "The right thoughts/movements occur of their own accord");
FlowSurveyQuestions.set("flowKnow", "I know what I have to do each step of the way");
FlowSurveyQuestions.set("flowControl", "I feel that I have everything under control");
FlowSurveyQuestions.set("flowLost", "I am completely lost in thought");
FlowSurveyQuestions.set("flowStake", "Something important to me is at stake here");
FlowSurveyQuestions.set("flowMistake", "I must not make any mistakes here");
FlowSurveyQuestions.set("flowWorried", "I am worried about failing");
FlowSurveyQuestions.set("flowCompare", "Compared to all other activities which I partake easy in, this one is difficult");
FlowSurveyQuestions.set("flowCompetence", "I think that my competence in this area is high");
FlowSurveyQuestions.set("flowDemand", "For me personally, the current demands are too high");

export const FlowSurveySequence = ["flowChallenge", "flowSmooth", "flowTime",
"flowConcentrate", "flowClear", "flowAbsorbed", "flowAccord",
"flowKnow", "flowControl", "flowLost", "flowStake", "flowMistake",
"flowWorried", "flowCompare", "flowCompetence", "flowDemand"];

export const UsabilitySurveyQuestions = new Map<string, string>();

UsabilitySurveyQuestions.set("tool", "Do you feel like the tool supported your performing the task?");
if (IsChronicles) {
  UsabilitySurveyQuestions.set("chronicles" , "Did you find the ability to navigate between histories helpful?");
}
UsabilitySurveyQuestions.set("delayFrustration" , "Were the delays frustrating?");
UsabilitySurveyQuestions.set("delayDifficulty" , "Did the delays make thing difficult?");

export const UsabilitySurveySequence = [
  "tool",
  "delayFrustration",
  "delayDifficulty",
  ...(IsChronicles ? ["chronicles"] : [])
];


