// the goal of training tasks is to make sure that people understand what is going on
// in this case they will do some simple ones

// gong to limit the number of years to 3.

export enum FeedbackType {
  Happy,
  Angry,
  Confused
}

export enum TaskStep {
  Description,
  Answering,
  Submitted,
}

export enum TaskTypes {
  ReadValue,
  SimpleCompareValue,
  ComplexCompareValue,
  PatternSearch,
  Trend,
  Recall,
}

export enum TaskConfidence {
  NotAtAll,
  Somewhat,
  Very
}

export type TaskQA = {
  // this is for logging
  qId: string,
  type: TaskTypes,
  question: string,
  // too much effort, changing to just select any that applies
  choices: {
    text: string,
    value: number
  }[],
  note?: string,
  linkedQId?: string;
};


