import * as React from "react";

import ScrubberView from "../ScrubberView";
import TaskQuestion from "./TaskQuestion";
import { trainingQA, taskQA } from "../../lib/experiment/experimentQAs";
import { diel } from "../../diel/setup";
import { TaskReward } from "../../lib/experiment/config";
import { LoggedTablesForAnalysis } from "../../../../shared/logHelper";

interface TrainingProps {
  isTraining: boolean;
  cancel: () => void;
  finish: () => void;
}

interface TrainingState {
  moneyEarned: number;
  step: number;
  control: boolean;
}

export default class TaskOrTrainingContainer extends React.Component<TrainingProps, TrainingState> {
  constructor(props: TrainingProps) {
    super(props);
    this.setControl = this.setControl.bind(this);
    this.nextTask = this.nextTask.bind(this);
    this.state = {
      moneyEarned: props.isTraining ? 0 : TaskReward * trainingQA.length,
      step: 0,
      control: true,
    };
    // this is a bit messy but oh well
    // bind diel with control
    diel.BindFreeze(this.setControl);
  }

  setControl(control: boolean) {
    this.setState({control});
  }

  nextTask() {
    // this is for logging and diel bookkeeping
    LoggedTablesForAnalysis.map(v => {
      // in theory anything that has cross dependencies
      // not very clean but don't really to be right now
      gpaas.logData({
        type: "table",
        name: v,
        values: diel.GetLogs(v)
      });
    });
    const length = this.props.isTraining ? trainingQA.length : taskQA.length;
    const isFinalStep = this.state.step === length - 1;
    // if (isFinalStep) {
    //   gpaas.logData({
    //     type: "table",
    //     name: "taskInfo",
    //     values: diel.GetLogs("taskInfo")
    //   });
    // }
    gpaas.nextTask();
    diel.ClearHistory();

    // this is for control flow
    if (isFinalStep) {
      this.props.finish();
      return;
    } else {
      this.setState(prev => {
        return {
          step: prev.step + 1,
          moneyEarned: prev.moneyEarned + TaskReward
        };
      });
    }
  }

  render() {
    let { isTraining } = this.props;
    let { step, moneyEarned } = this.state;
    // lets feed some fake data into the set
    let dashboard = <ScrubberView
      showIntro={isTraining}
    />;
    const qA = <TaskQuestion
      isTraining={isTraining}
      question={this.props.isTraining ? trainingQA[step] : taskQA[step]}
      nextTask={this.nextTask}
    />;
    // style={{float: "left"}}
    // using lineheight to align the button and the text
    return <>
      {dashboard}
      <div className="task-container">
        <div style={{textAlign: "center"}}>
          <button className="general-btn exit-btn" onClick={() => {
              const r = confirm("Are you sure you want to leave?");
              if (r === true) {
                this.props.cancel();
              } // otherwise do nothing
            }}>Finish and claim current reward of ${moneyEarned.toFixed(2)}
          </button>
        </div>
        {qA}
      </div>
    </>;
  }
}



