import * as React from "react";

import BarChart from "./Charts/BarChart";
import Scatterplot from "./Charts/Scatterplot";
import HappyOrNot from "./HappyOrNot";
import { ChronicleSlider } from "./ChronicleSlider";
import { IsChronicles } from "../lib/experiment/config";
import { diel } from "../diel/setup";
import { EventType } from "../lib/helper";
import { LoadingSummary } from "../lib/chart/chartHelper";
import { IntroSpec, IntroSteps } from "../lib/experiment/tutorialMaterial";

interface ScrubberViewProps {
  // itxIdOffset?: number;
  showIntro?: boolean;
  width?: number;
  height?: number;
}

interface ScrubberViewState {
  stateKey: string;
  clipped: boolean;
  focusItx: number;
  itxIdsInfo: LoadingSummary;
  control: boolean;
}

export default class ScrubberView extends React.Component<ScrubberViewProps, ScrubberViewState> {
  carrierChart: BarChart;
  stateChart: BarChart;
  dayChart: BarChart;
  delaysChart: Scatterplot;

  static defaultProps = {
    width: 800,
    height: 100,
    isTraining: false,
  };

  constructor(props: ScrubberViewProps) {
    super(props);
    // by default show the most recent unless otherwise specified
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.toggleClip = this.toggleClip.bind(this);
    // this is the refresh, not very sophisticated for now
    this.loadingSummary = this.loadingSummary.bind(this);
    this.focusItx = this.focusItx.bind(this);
    // this.setStateKey = this.setStateKey.bind(this);
    this.setControl = this.setControl.bind(this);
    this.state = {
      control: true,
      stateKey: "training",
      clipped: true,
      focusItx: null,
      itxIdsInfo: null,
    };
    // diel.SetStateRefresh(this.setStateKey);
    diel.BindFreeze(this.setControl);
  }

  setControl(control: boolean) {
    this.setState({control});
  }

  // setStateKey() {
  //   this.setState({stateKey: "task"});
  // }

  focusItx(focusItx: {itxId: number}) {
    this.setState({focusItx: focusItx.itxId});
  }

  loadingSummary(itxIdsInfo: LoadingSummary) {
    // console.log("loading summary called with info", itxIdsInfo);
    this.setState({itxIdsInfo});
  }

  componentWillMount() {
    if (IsChronicles) document.addEventListener("keydown", this.handleKeyDown);
  }

  componentWillUnmount() {
    if (IsChronicles) document.removeEventListener("keydown", this.handleKeyDown);
  }

  componentDidMount() {
    diel.BindOutput("loadingSummary", this.loadingSummary);
    diel.BindOutput("focusItx", this.focusItx, {singleRow: true});
    // by this point all the bound functions should have been called
    // need to force a refresh; a little bit brittle
    console.log("React all setup, calling tick");
    diel.tick()();
  }

  handleKeyDown(e: any) {
    // arrow up/down button should select next/previous list element
    if (e.keyCode === 38 || e.keyCode === 37) {
      diel.Input.deltaItx({$val: -1});
    } else if (e.keyCode === 40 || e.keyCode === 39) {
      diel.Input.deltaItx({$val: 1});
    }
  }

  toggleClip() {
    this.setState((prev) => {
      const clipped = !prev.clipped;
      diel.LogData.eventLog({
        $eventName: EventType[EventType.Clip],
        $parameter: clipped
      });
      return {clipped};
    });
  }

  // resetAll() {
  //   diel.Input.xBrushItx({$chart: });
  // }

  render() {
    let { showIntro } = this.props;
    let { focusItx, itxIdsInfo, clipped, control } = this.state;
    let delays =
        <Scatterplot
          ref={v => this.delaysChart = v}
          clipped={clipped}
          showIntro={showIntro}
          chart={"delays"}
          xLabel={"arrival delay"}
          yLabel={"departure delay"}
          key={"delays"}
        />;
    let state = <BarChart
          ref={v => this.stateChart = v}
          clipped={clipped}
          xLabel={"state"}
          yLabel={"count"}
          chart={"state"}
          isCategorical={true}
        />;
    let carrier = <BarChart
          ref={v => this.carrierChart = v}
          clipped={clipped}
          xLabel={"carrier"}
          yLabel={"count"}
          chart={"carrier"}
          isCategorical={true}
        />;
    let day = <BarChart
        ref={v => this.dayChart = v}
        clipped={clipped}
        xLabel={"day"}
        yLabel={"count"}
        chart={"day"}
        isCategorical={false}
        showIntro={showIntro}
      />;
    const slider = IsChronicles ? <ChronicleSlider
      itxIdsInfo={itxIdsInfo}
      focusItx={focusItx}
    /> : null;

    // so when the item is not specified, the field is omitted
    return <div key={this.state.stateKey} className={`vis-portal ${control ? "" : "dimmed"}`}>
      <div style={{float: "left"}}>
        <button
          className="general-btn vis-btn "
          style={{width: 75}}
          onClick={this.toggleClip}
          data-step={showIntro ? IntroSteps.indexOf("unclipButton") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("unclipButton") : null}
          >{this.state.clipped ? "" : "un"}zoom</button>
        {/* <button
          className="general-btn vis-btn "
          onClick={this.resetAll}
          data-step={showIntro ? IntroSteps.indexOf("resetAllBtn") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("resetAllBtn") : null}
          >reset filters</button> */}
        {IsChronicles
          ? <button
            className="general-btn vis-btn"
            onClick={diel.Input.deleteItx}
            data-step={showIntro ? IntroSteps.indexOf("historyButton") + 1 : null}
            data-intro={showIntro ? IntroSpec.get("historyButton") : null}
            >clear history</button>
          : null}
      </div>
      <div style={{float: "left"}}
          data-step={showIntro ? IntroSteps.indexOf("feedback") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("feedback") : null}
        >
          <HappyOrNot />
      </div>
      <br style={{clear: "both"}} />
      <div className="all-carts"
        data-step={showIntro ? IntroSteps.indexOf("sum") + 1 : null}
        data-intro={showIntro ? IntroSpec.get("sum") : null}
      >
        <div className="chart-layout-wrapper"
          data-step={showIntro ? IntroSteps.indexOf("clickState") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("clickState") : null}
        >
          {state}
        </div>
        <div className="chart-layout-wrapper"
          data-step={showIntro ? IntroSteps.indexOf("dayBar") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("dayBar") : null}
        >
          {day}
        </div>
        <div className="chart-layout-wrapper"
          data-step={showIntro ? IntroSteps.indexOf("carrierBar") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("carrierBar") : null}
        >
          {carrier}
        </div>
        <div className="chart-layout-wrapper"
          data-step={showIntro ? IntroSteps.indexOf("heatmap1") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("heatmap1") : null}
        >
          <div
            style={{overflow: "auto"}}
          >
            {delays}
          </div>
        </div>
        <div style={{clear: "both"}}></div>
      </div>
      <div
        data-step={showIntro && IsChronicles ? IntroSteps.indexOf("chronicles") + 1 : null}
        data-intro={showIntro && IsChronicles ? IntroSpec.get("chronicles") : null}
      >
      {slider}
      </div>
    </div>;
  }
}


