import * as React from "react";
import { holidayCount } from "../../lib/experiment/experimentQAs";


export const TaskIntro: React.StatelessComponent<{onClick: () => void}> = (p) => {
  return <>
    <h2>Analysis for the Holiday Season!🎄</h2>
    <p>
      You will now be viewing an interactive visualization over the same data but from the second half of the month.
      Again, we remind you to try to complete the tasks as quickly and as accurately as possible.
    </p>
    <p>
      There will be {holidayCount} tasks total. Good luck!
    </p>
    <button className="general-btn exp-nav-btn" onClick={p.onClick}>Start</button>
  </>;
};


