import * as React from "react";
import { SvgSpinner } from "./SvgSpinner";

import { LayoutInfo } from "../lib/chart/chartHelper";
import { diel } from "../diel/setup";

interface ChronicleSliderProps {
  itxIdsInfo: {itxId: number, loaded: boolean}[];
  focusItx: number;
}

// this will only be manaing the index, not the absolute, and so this is the same as the
// key up/down
export const ChronicleSlider: React.StatelessComponent<ChronicleSliderProps> = (p) => {
  const { focusItx, itxIdsInfo } = p;
  const { width } = LayoutInfo.timeLine;

  if (!itxIdsInfo) {
    // 10 is just a filler
    return <div style={{marginTop: 50, width: width, height: 10}}></div>;
  }

  // make the new distance deterministic, and make it grow left to right
  // scrolling's affordances are BAD, let it grow down
  // const minWidth = itxIdsInfo.length * height * 0.5;
  // const adjustedWidth = Math.max(minWidth, width);
  // const x = d3.scaleLinear()
  //             .domain([0, itxIdsInfo.length + 1])
  //             .rangeRound([30, adjustedWidth]);
  // focusItx === itx.itxId ? (height * 0.1 + 3) : height * 0.1
  const rectWidth = 30;
  const maxRectCols = Math.floor(width / rectWidth);
  const maxRectsRows = Math.ceil(itxIdsInfo.length / maxRectCols);
  const height = (maxRectsRows + 1) * rectWidth;

  const rects = itxIdsInfo.map((itx, i) => {
    const x = rectWidth * (i % maxRectCols);
    // the additional - 1 is because y start counting at the top
    const y = (maxRectsRows - Math.floor(i / maxRectCols) - 1) * rectWidth;
    return <><rect
      x={x}
      y={y}
      width={rectWidth - 5}
      height={rectWidth - 5}
      fill={focusItx === itx.itxId ? "orange" : "green"}
      // className={ "" : "flashing"}
      onClick={() => diel.Input.navigateItx({$xFilterItxId: itx.itxId})}
      style={{cursor: "pointer"}}
    ></rect>
    {!itx.loaded
      ? <SvgSpinner
          cx={x + rectWidth / 2}
          cy={y + rectWidth / 2}
          radius={ rectWidth / 2}
          color={"white"}
        />
      : null}
    <text
      x={x + 5}
      y={y + 15}
      font-size="15"
      style={{pointerEvents: "none"}}
      fill="white">
      {i === itxIdsInfo.length - 1 ? "N" : i + 1}
    </text></>;
  });
  // let lines =     <line
  //     x1={0} x2={width} y1={height * 0.3} y2={height * 0.3} stroke="#4CAF50" strokeWidth={3}>
  //   </line>;

  // "width={width} height={height} style={{overflowX: "scroll"}}
  // <div style={{marginTop: 50, width: width, height: height, overflowX: "scroll", overscrollBehavior: "none"}}>
  return <svg width={width} height={height}>
    {rects}
  </svg>;
};



