import * as React from "react";

interface ScatterCompareTutorialProps {
  title: string;
  prev: () => void;
  next: () => void;
}

export const ScatterCompareTutorial: React.StatelessComponent<ScatterCompareTutorialProps> = (p) => {
  let { prev, next, title } = p;
  return (
    <div className="popup">
      <div className="popup-inner">
        <div style={{padding: 10}}>
          <h3 style={{textAlign: "center"}}>{title}</h3>
          <p>
            The heatmap encodes a lot of information.
            We illustrate by reading two different views of the same filtered data.
            On the left, we have the "unzoomed" view of the data.
            The rightmost distribution of delayed flights count
            (red and white bars) shows that only a tiny portion of the flights are delayed.
            The heat map to the left shows more detail---how delayed are the delayed flights.
            However, it is somewhat difficult to read witht the gray in the background. This is when you can "zoom," which brings us to the image to the right.
          </p>
          <p>
            For the image to the right, the gray background data is no longer plotted (hence "zoomed" in).
            While easy to read individually, it makes it difficult to compare across charts, since the scale is changing.
          </p>
          <img width={300} src="https://logical-interactions.github.io/chroniclesv2/dist/heat_unzoomed.png"></img>
          <img width={300} src="https://logical-interactions.github.io/chroniclesv2/dist/heat_zoomed.png"></img>
          <button className="general-btn" onClick={prev}>Prev</button>
          <button className="general-btn exp-nav-btn" onClick={next}>Finish Techniques</button>
        </div>
      </div>
    </div>
  );
};



