import * as React from "react";
import { CancelReason } from "../../lib/experiment/experiment";


interface CancelSurveyProps {
  nextStep: (data: any) => void;
}

interface CancelSurveyState {
  choices: CancelReason[];
  comment: string;
  warn: boolean;
}

export default class CancelSurvey extends React.Component<CancelSurveyProps, CancelSurveyState> {
  constructor(props: CancelSurveyProps) {
    super(props);
    this.onCommentChange = this.onCommentChange.bind(this);
    this.nextStep = this.nextStep.bind(this);
    this.toggleChoices = this.toggleChoices.bind(this);
    this.state = {
      choices: [],
      comment: "",
      warn: false,
    };
  }

  onCommentChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
    this.setState({ comment: event.target.value });
  }

  toggleChoices(c: CancelReason) {
    this.setState(prev => {
      const idx = prev.choices.indexOf(c);
      if (idx > -1) {
        prev.choices.splice(idx, 1);
      } else {
        prev.choices.push(c);
      }
      return prev;
    });
  }

  nextStep() {
    // check for null
    const { choices } = this.state;
    if (choices.length === 0) {
      this.setState({warn: true});
    } else {
      this.props.nextStep({
        choices: this.state.choices.join(", "),
        comment: this.state.comment
      });
    }
  }

  render() {
    const { choices } = this.state;
    const toggleChoices = this.toggleChoices;
    function _genRadio(c: CancelReason, text: string) {
      return <label><input
        required={true}
        type="checkbox"
        value={CancelReason[c]}
        checked={choices.indexOf(c) > -1}
        onChange={() => toggleChoices(c)}
      ></input>{text}
      </label>;
    }
    const choicesDiv = <>
      <p className="question">Why did you decide to cancel?</p>
      <div className="radio-button-wrapper">
        {_genRadio(CancelReason.TasksTooHard, "Tasks are too hard")}
        {_genRadio(CancelReason.DelaysFrustrating, "Delays are frustrating")}
        {_genRadio(CancelReason.PayTooLow, "Pay is too low")}
        {_genRadio(CancelReason.Other, "Other (please specify below)")}
      </div>
    </>;
    return <>
      {this.state.warn === true
        ? <p className="warning">
          Please select at least one choice.</p>
        : null}
      {choicesDiv}
      <label htmlFor="comments"><i>(Optional)</i> Any additional comments?</label>
      <br/>
      <textarea
        rows={5}
        cols={39}
        id="comments"
        name="comments"
        onChange={this.onCommentChange}>
      </textarea>
      <br/>
      <button
      className="general-btn exp-nav-btn"
      onClick={() => this.nextStep()}>
        Submit and Exit
      </button>
    </>;
  }

}


