export enum CancelReason {
  TasksTooHard,
  DelaysFrustrating,
  PayTooLow,
  Other
}


export type ExperimentUnitParams = {
  qId: string;
};

/**
 * Returns the value associated with the given URL parameter name, otherwise
 * returns null if no such parameter value is set.
 * @param {string} sParam
 * @returns {string}
 */
export function getUrlParameter(sParam: string) {
  let sPageURL = decodeURIComponent(window.location.search.substring(1)),
    sURLVariables = sPageURL.split("&"),
    sParameterName,
    i;

  for (i = 0; i < sURLVariables.length; i++) {
    sParameterName = sURLVariables[i].split("=");

    if (sParameterName[0] === sParam) {
      return sParameterName[1] === undefined ? null : sParameterName[1];
    }
  }
  return null;
}

export function setupExperimentForGpaas(numTasks: number) {
  console.log("setting up experiment for", numTasks);
  return () => {
    function clearTask() {}
    function finish(opt: any) {
      const submitTo = getUrlParameter("turkSubmitTo");
      if (DEBUG || submitTo === "sandbox") {
        alert("finished all tasks");
      } else {
        opt.submit(); // submit HIT
      }
    }
    // function trainingTask() {}
    function viewTask() {}
    // FIXME: these are wrong
    return {
      name: ExperimentName,
      task: ExperimentName,
      researcher: Researcher,
      numTasks,
      params: {
        params: [{ name: "p",  type: "UniformChoice", options: [0] }]
      },
      clearTask: clearTask,
      finish: finish,
      trainingTasks: [] as any, // () => trainingTask
      viewTask: viewTask,
    };
  };
}



