import * as d3 from "d3";
import * as React from "react";

import { ResetButton } from "../ResetButton";
import { diel } from "../../diel/setup";
import { lightGray, darkGray, scatterDark, scatterLight,
  ScatterPlotDataType, RectSection, LayoutInfo,
  SharedChartProps, DefaultChartProps, IsLoading } from "../../lib/chart/chartHelper";
import { IntroSpec, IntroSteps } from "../../lib/experiment/tutorialMaterial";
import { DEBUG } from "../../lib/experiment/config";

interface ScatterplotProps extends SharedChartProps {
  clipped: boolean;
  xDomain?: [number, number];
  yDomain?: [number, number];
}

interface ScatterplotState {
  hoverInfo: {xPos: number, yPos: number, value: number};
  baseData: ScatterPlotDataType;
  isLoading: IsLoading;
  selection?: RectSection;
  data?: ScatterPlotDataType;
}

export default class Scatterplot extends React.Component<ScatterplotProps, ScatterplotState> {
  static defaultProps = DefaultChartProps;

  constructor(props: ScatterplotProps) {
    super(props);
    this.refreshSelection = this.refreshSelection.bind(this);
    this.refreshData = this.refreshData.bind(this);
    this.refreshIsLoading = this.refreshIsLoading.bind(this);
    this.clickRect = this.clickRect.bind(this);
    this.state = {
      baseData: diel.GetStaticView(`${props.chart}ChartInitData`) as ScatterPlotDataType,
      isLoading: {status: 1},
      hoverInfo: null
    };
  }

  refreshSelection(selection: RectSection) {
    this.setState({ selection });
  }

  refreshData(data: ScatterPlotDataType) {
    this.setState({
      data,
      // piggy back the reset
      hoverInfo: null
    });
  }

  refreshIsLoading(isLoading: IsLoading) {
    // console.log("isLoading scatter", isLoading);
    this.setState({ isLoading });
  }

  componentDidMount() {
    let { chart } = this.props;
    diel.BindOutput(`${chart}ChartDataView`, this.refreshData);
    diel.BindOutput(`${chart}ChartSelection`, this.refreshSelection, {singleRow: true});
    diel.BindOutput(`${chart}ChartIsLoading`, this.refreshIsLoading, {singleRow: true});
  }

  clickRect(value: number, xPos: number, yPos: number) {
    return () => {
      this.setState({hoverInfo: {value, xPos, yPos}});
    };
  }

  render() {
    const { chart, xLabel, yLabel, clipped, showIntro } = this.props;

    if (!(this.state && this.state.baseData) || (this.state.baseData.length === 0))  {
      throw new Error("should be here already");
    }
    const { width, height, marginBottom, marginLeft, marginRight, marginTop} = LayoutInfo[chart];
    const { selection, data, isLoading, baseData } = this.state;

    const chartWidth = width - marginLeft - marginRight;
    const chartHeight = height - marginTop - marginBottom;
    // if true, use data
    const unclippable = (!clipped) && data && (data.length > 0);
    let label;
    let brush: any = null;
    // let content: JSX.Element = null;
    let axesLabels;
    let xDomain = this.props.xDomain ? this.props.xDomain : [
      // force it to be zero
      0, // d3.min(baseData.map(d => d.x))
      // force the additional padding, bigger to make selection easier
      unclippable ? d3.max(data.map(d => d.x)) + 40 : d3.max(baseData.map(d => d.x)) + 40
    ];

    const iColor = d3.interpolateRgb(scatterLight, scatterDark);
    // const iColor = d3.interpolateRgb("rgb(245, 251, 239)", "rgb(13, 84, 148)");
    const iBaseColor = d3.interpolateRgb(lightGray, darkGray);

    let yDomain = this.props.yDomain ? this.props.yDomain : [
        0, // d3.min(baseData.map(d => d.y)), // force 0
        unclippable ? d3.max(data.map(d => d.y)) + 40 : d3.max(baseData.map(d => d.y)) + 40
      ];
    // note that vDomain remains that of baseData regardless of clipping
    let vDomain = [d3.min(baseData.map(d => d.v)), d3.max(baseData.map(d => d.v))];
    const x = d3.scaleLinear()
                .domain(xDomain)
                .rangeRound([0, chartWidth]);

    // hard coding 20 here
    const rectWidth = Math.abs(x(20) - x(0));
    const y = d3.scaleLinear()
                .domain(yDomain)
                .rangeRound([chartHeight, 0]);
    const rectHeight = Math.abs(y(20) - y(0));
    const v = d3.scaleLog()
                .domain(vDomain)
                .range([0, 1]);
    if (selection && selection.low) {
      if (DEBUG && isNaN(y(selection.high2))) {
        throw new Error("y scale must get valid result");
      }
      label = (
        <rect
          className="selector"
          x={x(selection.low)}
          y={y(selection.high2)}
          width={x(selection.high) - x(selection.low)}
          height={Math.abs(y(selection.high2) - y(selection.low2))}
          fill={"gray"}
          fill-opacity="0.3"
        ></rect>
      );
    }
    const axisBottom = d3.axisBottom(x).ticks(5, "d");
    const axisLeft = d3.axisLeft(y).ticks(10, "d");

    // generate a brush that interacts with the chart
    brush = d3.brush()
    .extent([[0, 0], [chartWidth, chartHeight]])
    .on("end", function() {
      // see https://github.com/d3/d3-brush/issues/10
      if (!d3.event.sourceEvent) return; // Only transition after input.
      if (!d3.event.selection) return; // Ignore empty selections.
      // [[x0, y0], [x1, y1]],
      const s = d3.brushSelection(this) as [[number, number], [number, number]];
      if (s !== null) {
        let low = Math.min(x.invert(s[0][0]), x.invert(s[1][0]));
        let high = Math.max(x.invert(s[0][0]), x.invert(s[1][0]));
        let low2 = Math.min(y.invert(s[1][1]), y.invert(s[0][1]));
        let high2 = Math.max(y.invert(s[1][1]), y.invert(s[0][1]));
        diel.Input.xBrushItx({$low: low, $high: high, $low2: low2, $high2: high2, $chart: chart});
      }
      d3.select(this).call(brush.move, null);
    });
    const brushDiv = <g ref={ g => d3.select(g).call(brush) }></g>;
    let delayDistribution: JSX.Element;
    const baseRects = unclippable ? [] : baseData.map((d) => {
      const xPos = x(d.x);
      const yPos = y(d.y + 20);
      if (DEBUG && isNaN(yPos)) {
        throw new Error("y scale must get valid result");
      }
      return <rect
        key={`${d.x}_${d.y}_base_heat`}
        fill={iBaseColor(v(d.v))}
        fillOpacity={0.8}
        x={xPos}
        y={yPos}
        width={rectWidth}
        height={rectHeight}
        // it's ok that there will be overlap, since the mouse event does not go down z-index
        onClick={this.clickRect(d.v, xPos, yPos)}
      ></rect>;
    });
    let rects: JSX.Element[];
    if (data) {
      rects = data.map((d) => {
      const xPos = x(d.x);
      const yPos = y(d.y + 20);
      if (DEBUG && isNaN(yPos)) {
        throw new Error("y scale must get valid result");
      }
      // getting rid of the title since it's so cluncky<title>{d.v}</title>
      return <rect
        key={`${d.x}_${d.y}_filter_heat`}
          fill={iColor(v(d.v))}
          className="hoverable"
          fillOpacity={0.8}
          x={xPos}
          y={yPos}
          width={rectWidth}
          height={rectHeight}
          onClick={this.clickRect(d.v, xPos, yPos)}
        ></rect>;
      });
      // also add the vertical bar that shows the relative distribution
      // first push the count of delays
      // we could do this in SQL if this becomes too slow
      const delayCount = data.filter(d => d.x > 0).reduce((a, b) => a + b.v, 0);
      const totalCount = data.reduce((a, b) => a + b.v, 0);
      // console.log("total flights", totalCount, delayCount);
      const annotationWidth = 10;
      const annotationHeight = chartHeight  * 2 / 3;
      delayDistribution = <svg
          width={20}
          height={height}
        >
        <rect
          className="delayDistributionRect"
          x={5}
          y={chartHeight / 6}
          width={annotationWidth}
          height={delayCount / totalCount * annotationHeight}
          fill={"red"}
          stroke={"black"}
          strokeWidth={1}
        ></rect>
        <rect
          className="delayDistributionRect"
          x={5}
          y={chartHeight / 6 + delayCount / totalCount * annotationHeight}
          width={annotationWidth}
          height={(1 - delayCount / totalCount) * annotationHeight}
          fill={"white"}
          stroke={"black"}
          strokeWidth={1}
        ></rect>
      </svg>;
    }
    if (this.state.hoverInfo && this.state.hoverInfo.xPos && this.state.hoverInfo.yPos) {
      rects.push(<rect
        key={"clicked_sactter"}
        x={this.state.hoverInfo.xPos}
        y={this.state.hoverInfo.yPos}
        width={rectWidth}
        height={rectHeight}
        style={{fill: "none", stroke: "black", strokeWidth: 2}}
      ></rect>);
    }

    const ticks = <>
      <g ref={(g) => d3.select(g).call(axisBottom)}
        transform={`translate(0, ${chartHeight})`}></g>
      <g ref={(g) => d3.select(g).call(axisLeft)}></g></>;
    axesLabels = (
      <g>
        <text
          className="chart-label"
          y={height - 5}
          x={(chartWidth / 2) + marginLeft}
          textAnchor="middle"
        > {xLabel}
        </text>
        <text
          className="chart-label"
          x={10}
          y={height / 2}
          transform={`rotate(-90, ${10}, ${height / 2})`}
          textAnchor="middle"
        > {yLabel}
        </text>
      </g>);

    const content = <svg width={width} height={height}
      data-step={showIntro ? IntroSteps.indexOf("heatmap2") + 1 : null}
      data-intro={showIntro ? IntroSpec.get("heatmap2") : null}
      >
        <g transform={"translate(" + marginLeft + "," + marginTop + ")"}>
          {ticks}
          {brushDiv}
          {baseRects}
          {rects}
          {this.state.hoverInfo
            ? <text
                className="tooltip"
                fontStyle="bold"
                x={this.state.hoverInfo.xPos - 3}
                y={this.state.hoverInfo.yPos - 5}
              >{this.state.hoverInfo.value}</text>
            : null}
          {label}
        </g>
        {axesLabels}
      </svg>;
    const legend = <svg
      width={25}
      height={height}
      data-step={showIntro ? IntroSteps.indexOf("delayLegend") + 1 : null}
      data-intro={showIntro ? IntroSpec.get("delayLegend") : null}
    >
    <defs>
      <linearGradient id="linear" x1="0%" y1="0%" x2="0%" y2="100%">
        <stop offset="0%"   stop-color={scatterLight}/>
        <stop offset="100%" stop-color={scatterDark}/>
      </linearGradient>
    </defs>
    <rect x={5} y={20} width={5} height={height - 60} fill="url(#linear)" />
    <text x={5} y={10} fontSize={10}>{vDomain[0]}</text>
    <text x={0} y={height - 30} fontSize={10}>{vDomain[1]}</text>
  </svg>;
    const loadingClass = isLoading.status ? "indicatorLine" : "";
    return (
      <div className="scatter-chart chart-container">
        {content}
        {legend}
        <div
          style={{overflow: "auto", display: "inline-block"}}
          data-step={showIntro ? IntroSteps.indexOf("delayDistribution") + 1 : null}
          data-intro={showIntro ? IntroSpec.get("delayDistribution") : null}
        >
        {delayDistribution}
        </div>
        <div style={{height: 5}} className={loadingClass}></div>
        <ResetButton
          width={width}
          disabled={!(selection && selection.low)}
          chart={chart}
          selection={(selection && selection.low) ? "brush" : ""}
        />
      </div>
    );
  }
}


