import * as React from "react";

import ScrubberView from "../ScrubberView";
import { VideoPopup } from "./VideoPopup";
import { ScatterCompareTutorial } from "./ScatterCompareTutorial";

import { IntroSteps, IntroSpec } from "../../lib/experiment/tutorialMaterial";
import { videoTutorials } from "../../lib/experiment/tutorialMaterial";

interface TutorialProps {
  onClick: () => void;
}

interface TutorialState {
  step: number;
  introTour: any;
  showPopup: boolean;
}


export default class Tutorial extends React.Component<TutorialProps, TutorialState> {
  constructor(props: TutorialProps) {
    super(props);
    this.startAgain = this.startAgain.bind(this);
    this.setStep = this.setStep.bind(this);
    this.state = {
      // meh can't be bothered to create enums
      step: -1,
      introTour: null,
      showPopup: false,
    };
  }

  componentDidMount() {
    const introTour = introJs().setOptions({
      exitOnOverlayClick: false
    });
    introTour.start();
    this.setState({introTour});
  }

  setStep(step: number) {
    this.setState({step});
  }

  startAgain() {
    this.state.introTour.start();
  }

  render() {
    const s = this.setStep;
    const { step } = this.state;
    let content: JSX.Element;
    if (step === 0) {
      content = <div className="popup">
      <div className="popup-inner">
        <div style={{margin: "auto", maxWidth: 600}}>
          <p>
            To help you make use of the tool better, complete the tasks faster, and a higher chance at extra bonus,
            we will show you {videoTutorials.length + 1} additional training modules (<b>no</b> audio required).
          </p>
          <p>
          Each part should take about a minute or less.
          Also, some of the training may appear to repeat the previous instruction---we just want to make sure you get it!
          </p>
          <button  className="general-btn exp-nav-btn" onClick={() => s(1)}>Start</button>
        </div>
      </div>
    </div>;
    } else if (step > 0 && step < videoTutorials.length + 1) {
      const tut = videoTutorials[step - 1];
      content = <VideoPopup
          title={tut.title + ` (${step}/${videoTutorials.length + 1})`}
          videoLink={tut.videoLink}
          texts={tut.texts}
          prev={(step > 0) ? () => s(step - 1) : null}
          next={() => s(step + 1)}
        />;
    } else if (step === videoTutorials.length + 1) {
      content = <ScatterCompareTutorial
          title={`How to Read and Compare Delay Heatmaps (${step}/${videoTutorials.length + 1})`}
          prev={() => s(step - 1)}
          next={() => s(step + 1)}
        />;
    }
    return <>
        <ScrubberView
          showIntro={true}
        />
        <div style={{float: "left", paddingLeft: 20}}>
          {content}
          <button
            className="tour-btn general-btn"
            onClick={this.startAgain}
            data-step={IntroSteps.indexOf("tutorial") + 1}
            data-intro={IntroSpec.get("tutorial")}
          >Show Tour Again</button>
          <hr/>
          <div
            data-step={IntroSteps.indexOf("video") + 1}
            data-intro={IntroSpec.get("video")}
          >
          {step === videoTutorials.length + 2
            ? <button className="general-btn exp-nav-btn" onClick={this.props.onClick}>Start Tasks</button>
            : <button  className="general-btn exp-nav-btn" onClick={() => s(0)}>Start Techniques</button>}
          </div>
        </div>
    </>;
  }
}


