/// <reference path="../../../external/gpaas.d.ts" />

import * as React from "react";

import Survey from "./Survey";
import Demographics from "./Demographics";
import TaskOrTrainingContainer from "./TaskContainer";
import { TrainingIntro } from "./TrainingIntro";
import { TaskIntro } from "./TaskIntro";
import Instructions from "./Instructions";
import Tutorial from "./Tutorial";

import { getUrlParameter, ExperimentStep, setupExperimentForGpaas } from "../../lib/experiment/experiment";
import { FlowSurveyQuestions, FlowSurveySequence, UsabilitySurveyQuestions, UsabilitySurveySequence } from "../../lib/experiment/survey";
import { diel } from "../../diel/setup";
import { trainingQA, TotalTaskCount } from "../../lib/experiment/experimentQAs";
import { EventType } from "../../lib/helper";
import { IsChronicles, ExpVersion, DEBUG } from "../../lib/experiment/config";
import CancelSurvey from "./CancelSurvey";

interface ExperimentContainerState {
  step: ExperimentStep;
  assignmentId: string;
  focusWarning: boolean;
  focusWarningShown: number; // hacking together a throttle
}

export default class ExperimentContainer extends React.Component<{}, ExperimentContainerState> {
  constructor(props: undefined) {
    super(props);
    this.start = this.start.bind(this);
    this.submitDemographic = this.submitDemographic.bind(this);
    this.startTutorial = this.startTutorial.bind(this);
    this.startTraining = this.startTraining.bind(this);
    this.finishTraining = this.finishTraining.bind(this);
    this.startTasks = this.startTasks.bind(this);
    this.finishTasks = this.finishTasks.bind(this);
    this.finishFlowSurvey = this.finishFlowSurvey.bind(this);
    this.finishToolSurvey = this.finishToolSurvey.bind(this);
    this.actuallyCancel = this.actuallyCancel.bind(this);
    this.cancelTasks = this.cancelTasks.bind(this);
    this.startSurvey = this.startSurvey.bind(this);
    const assignmentId = getUrlParameter("assignmentId");
    this.state = {
      step: ExperimentStep.Introduction,
      assignmentId,
      focusWarning: false,
      focusWarningShown: null
    };
  }

  componentDidMount() {
    window.addEventListener("focus", () => {
      this.setState({focusWarning: false});
      diel.LogData.eventLog({$eventName: EventType[EventType.Focus]});
    });
    window.addEventListener("blur", () => {
      // show pop up if during task event
      const t = diel.IsDuringTask();
      if (t.firstTime && (this.state.focusWarningShown === null || this.state.focusWarningShown < Date.now() - 10000)) {
        this.setState({
          focusWarningShown: Date.now(),
        });
        alert(`Please try not to leave during your next task!`);
      } else if (t.duringTask) {
        this.setState({focusWarning: true});
      }
      diel.LogData.eventLog({$eventName: EventType[EventType.Blur]});
    });
  }

  _logStep(s: ExperimentStep) {
    diel.LogData.eventLog({
      $eventName: "step",
      $parameter: ExperimentStep[s]
    });
  }

  start() {
    // this includes the surveys as well
    // 3 surveys: demographic, flow and usability
    const e = gpaas.startExperiment(setupExperimentForGpaas(TotalTaskCount));
    if (e.setupSuccessful) {
      console.log("experiment started succesfully");
      diel.LogData.eventLog({
        $eventName: EventType[EventType.IsChronicles],
        $parameter: IsChronicles ? "true" : "false"
      });
      diel.LogData.eventLog({
        $eventName: EventType[EventType.ExpVersion],
        $parameter: ExpVersion
      });
      this._logStep(ExperimentStep.TrainingIntro);
      this.setState({ step: ExperimentStep.TrainingIntro });
    } else {
      console.log("failed to start experiment");
    }
  }

  startTutorial() {
    this._logStep(ExperimentStep.Tutorial);
    this.setState({ step: ExperimentStep.Tutorial });
  }

  startTraining() {
    this._logStep(ExperimentStep.Training);
    diel.ClearHistory();
    this.setState({ step: ExperimentStep.Training });
  }

  finishTraining() {
    // also setup db
    // check if the answers were all wrong
    const zeroCount = diel.TrainingZeroCount();
    if (zeroCount === trainingQA.length) {
      gpaas.cancelTasks();
      this._logStep(ExperimentStep.FailedTraining);
      this.setState({ step: ExperimentStep.FailedTraining});
      // this.cancelTasks();
    } else {
      this._logStep(ExperimentStep.TasksIntro);
      diel.ChangeToTask();
      this.setState({ step: ExperimentStep.TasksIntro });
    }
  }

  startTasks() {
    this._logStep(ExperimentStep.Tasks);
    this.setState({ step: ExperimentStep.Tasks });
  }

  finishTasks() {
    // then make gpaas send it to server
    this._logStep(ExperimentStep.SurveyIntro);
    this.setState({ step: ExperimentStep.SurveyIntro });
  }

  startSurvey() {
    this._logStep(ExperimentStep.FlowSurvey);
    this.setState({ step: ExperimentStep.FlowSurvey });
  }

  _logSurvey(data: any, s: ExperimentStep) {
    const values = Object.keys(data).filter(k => k !== "warn")
      .map(k => ({
        qId: k,
        answer: data[k]
      }));
    gpaas.logData({
      type: "table",
      name: ExperimentStep[s],
      values
    });
  }

  finishFlowSurvey(data: any) {
    this._logStep(ExperimentStep.FlowSurvey);
    this._logSurvey(data, ExperimentStep.FlowSurvey);
    gpaas.nextTask();
    this.setState({ step: ExperimentStep.ToolSurvey });
  }

  finishToolSurvey(data: any) {
    this._logStep(ExperimentStep.ToolSurvey);
    this._logSurvey(data, ExperimentStep.ToolSurvey);
    gpaas.nextTask();
    this.setState({ step: ExperimentStep.Demographics });
  }

  submitDemographic(data: any) {
    this._logStep(ExperimentStep.Demographics);
    this._logSurvey(data, ExperimentStep.Demographics);
    gpaas.nextTask();
    this.setState({ step: ExperimentStep.End });
  }

  cancelTasks() {
    this._logStep(ExperimentStep.CancelSurvey);
    this.setState({ step: ExperimentStep.CancelSurvey });
  }

  actuallyCancel(data: any) {
    this._logSurvey(data, ExperimentStep.CancelSurvey);
    gpaas.nextTask();
    gpaas.cancelTasks();
    this.setState({ step: ExperimentStep.End });
    console.log("experiment cancelled");
  }

  render() {
    let { step, assignmentId } = this.state;
    let content: JSX.Element;
    if (step === ExperimentStep.Introduction) {
      content = <Instructions
        onClick={this.start}
        disabled={assignmentId === "ASSIGNMENT_ID_NOT_AVAILABLE"}
      />;
    } else if (step === ExperimentStep.Demographics) {
      content = <Demographics
        onClick={this.submitDemographic}
        />;
    } else if (step === ExperimentStep.TrainingIntro) {
      content = <TrainingIntro
        onClick={this.startTutorial}
      />;
    } else if (step === ExperimentStep.Tutorial) {
      content = <Tutorial
        onClick={this.startTraining}
      />;
    }
    else if (step === ExperimentStep.Training) {
      content = <TaskOrTrainingContainer
        isTraining={true}
        cancel={this.cancelTasks}
        finish={this.finishTraining}
      />;
    } else if (step === ExperimentStep.TasksIntro) {
      content = <TaskIntro
        onClick={this.startTasks}
      />;
    } else if (step === ExperimentStep.Tasks) {
      content = <TaskOrTrainingContainer
        isTraining={false}
        cancel={this.cancelTasks}
        finish={this.finishTasks}
      />;
    } else if (step === ExperimentStep.SurveyIntro) {
      content = <div>
        <h3>You are almost done!</h3>
        <p>
          We just need you to fill out three more quick surveys (each should take less than a minute).
          Don't worry if you are confused about exactly what the question means---try your best to provide an answer.
          If you are still worried, let us know in the comment section.
        </p>
        <button className="general-btn exp-nav-btn" onClick={this.startSurvey}>Start Surveys</button>
      </div>;
    } else if (step === ExperimentStep.FlowSurvey) {
      content = <Survey
        onClick={this.finishFlowSurvey}
        surveyQuestions={FlowSurveyQuestions}
        surveySequence={FlowSurveySequence}
        freeformPrompt={"Feel free to comment on your experience completing the tasks here."}
      />;
    } else if (step === ExperimentStep.ToolSurvey) {
      content = <Survey
        onClick={this.finishToolSurvey}
        surveyQuestions={UsabilitySurveyQuestions}
        surveySequence={UsabilitySurveySequence}
        freeformPrompt={"Feel free to comment on the tools or the effect of latency here. You can also share your ideas on how to improve, if you like."}
      />;
    } else if (step === ExperimentStep.CancelSurvey) {
      content = <CancelSurvey nextStep={this.actuallyCancel}/>;
    } else if (step === ExperimentStep.End) {
      content = <>
          <h2>Thank you! All complete. You may close and leave the page now.</h2>
          <p>We will be sure to pay you within 2 weeks time!</p>
        </>;
    } else if (step === ExperimentStep.FailedTraining) {
      content = <>
        <h2>Failed to qualitfy for complete HIT</h2>
        <p>Thank you for attempting our HIT --- unfortuantely you got all three training
        questions wrong and are as a result not eligible to participate.
        You will be paid out for the tasks you have completed.</p>
        </>;
    } else {
      if (DEBUG) throw new Error(`step not handled: ${ExperimentStep[step]}`);
    }
    return <>
      {this.state.focusWarning ? <p className="warning">Please stay on the page during your task</p> : null }
      {content}
    </>;
  }
}


