import * as React from "react";
import { diel } from "../diel/setup";
import { EventType } from "../lib/helper";
import { FeedbackType } from "../lib/experiment/tasks";

interface HappyOrNotState {
  lastClick: number;
}

export default class HappyOrNot extends React.Component<{}, HappyOrNotState> {

  constructor(props: null) {
    super(props);
    this.state = {
      lastClick: null
    };
  }

  onClick(paramter: FeedbackType) {
    return () => {
      this.setState({lastClick: Date.now()});
      setTimeout(() => {
        if (this.state.lastClick < Date.now() - 3000) {
          this.setState({lastClick: null});
        }
      }, 3001);
      // diel log
      diel.LogData.eventLog({
        $eventName: EventType[EventType.HappyOrNot],
        $parameter: FeedbackType[paramter]
      });
    };
  }

  render() {
    return <div>
      {/* <p style={{display: "inline-block"}} >How do you feel using the tool right now?</p> */}
      <button className="happyOrNotBtn zoomOnHover" onClick={this.onClick(FeedbackType.Happy)}>😃</button>
      <button className="happyOrNotBtn zoomOnHover" onClick={this.onClick(FeedbackType.Angry)}>😡</button>
      <button className="happyOrNotBtn zoomOnHover" onClick={this.onClick(FeedbackType.Confused)}>😵</button>
      {this.state.lastClick ? <p style={{display: "inline"}}>Got your feedback!</p> : null}
    </div>;
  }
}


