import { LatencyOption } from "../helper";

export const Researcher = "chroniclesv2";
export const ExperimentName = "chroniclesv2draft";

export const ExpVersion = "V0";
export const IsProd = true;
export const FilePathPrefix = IsProd ? "https://logical-interactions.github.io/chroniclesv2/dist/" : "./dist/";
export const DEBUG = true && !IsProd; // if its prod debug must be false
export const IsChronicles = true;
export const ExperimentLatencyOption = LatencyOption.High;

export const BonusReward = 3;
export const ExtraBonusReward = 3;
export const TaskReward = 0.2;

export function judgeScreenSize() {
  return (screen.width < 1080) || (screen.height < 800);
}

export function judgeWindowSize() {
  return (window.innerWidth < 1000 || window.innerHeight < 450);
}


