import * as React from "react";

interface SurveyProps {
  onClick: (data: any) => void;
  surveyQuestions: Map<string, string>;
  surveySequence: string[];
  freeformPrompt: string;
}

interface SurveyState { [index: string]: string; }

function radioHelper(
  surveyQuestions: Map<string, string>,
  i: number,
  name: string,
  state: {[index: string]: string},
  onChange: (event: any) => void
) {
  const question = surveyQuestions.get(name);
  const stateVal = state[name];
  const radio = new Array(5).fill(0).map((_, i) => <td style={{align: "right"}}>
      <input
        required={i === 0}
        type="radio"
        style={{align: "left"}}
        name={name}
        value={i + 1}
        checked={stateVal === `${i + 1}`}
        onChange={onChange}
      />
    </td>);
  return (<>
    <tr>
      <td style={{width: 500}}>{i + 1}. {question}</td>
      {radio}
    </tr>
 </>);
}

export default class Survey extends React.Component<SurveyProps, SurveyState> {
  constructor(props: SurveyProps) {
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onClick = this.onClick.bind(this);
    this.state = {
      warn: "false", // hack because state was to work with other survey items
    };

  }

  onChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
    this.setState({ [event.target.name]: event.target.value });
  }

  onClick() {
    // make sure that they are selected
    // check how many keys there are that's not comments
    const numAnswers = Object.keys(this.state).filter(k => k !== "comments").length;
    if (numAnswers < this.props.surveyQuestions.size) {
      this.setState({warn: "true"});
    } else {
      this.setState({warn: "false"});
      this.props.onClick(this.state);
    }
  }

  render() {
    const { surveyQuestions, surveySequence, freeformPrompt } = this.props;

    let questions = surveySequence.map((q, i) => {
      return radioHelper(surveyQuestions, i, q, this.state, this.onChange);
    });
    return (
      <div>
        {this.state.warn === "true"
          ? <p className="warning">
            Please answer all of the multiple choice questions.</p>
          : null}
        <table style={{width: 800, borderBottom: "solid"}}>
          <tr>
            <th style={{width: 500}}>Question</th>
            <th style={{align: "left", width: 100}}>Not at all</th>
            <th style={{align: "left", width: 100}}>Partly</th>
            <th style={{align: "left", width: 100}}>Very much</th>
          </tr>
        </table>
        <table style={{width: 800}}>
          {questions}
        </table>
        <br/>
        <div className="question-wrapper" style={{width: 800}}>
          <label htmlFor="comments">Optional feedback: {freeformPrompt}</label>
          <br/>
          <textarea
            rows={5}
            cols={50}
            id="comments"
            name="comments"
            onChange={this.onChange}></textarea>
        </div>
        <button className="general-btn exp-nav-btn" onClick={this.onClick}>Submit Survey and Continue</button>
      </div>
    );
  }
}


